import React, { useEffect, useState } from "react";
import Empty from "../components/Empty";
import Footer from "../components/Footer";
import Navbar from "../components/Navbar";
import fetchData from "../helper/apiCall";
import { setLoading } from "../redux/reducers/rootSlice";
import Loading from "../components/Loading";
import { useDispatch, useSelector } from "react-redux";
import jwt_decode from "jwt-decode";
import axios from "axios";
import toast from "react-hot-toast";
import jsPDF from "jspdf";
import html2canvas from "html2canvas";
import "../styles/user.css";

const Appointments = () => {
  const [appointments, setAppointments] = useState([]);
  const dispatch = useDispatch();
  const { loading } = useSelector((state) => state.root);
  const { userId } = jwt_decode(localStorage.getItem("token"));

  const getAllAppoint = async () => {
    try {
      dispatch(setLoading(true));
      const temp = await fetchData(`/appointment/getallappointments?search=${userId}`);
      setAppointments(temp);
      dispatch(setLoading(false));
    } catch (error) {
      dispatch(setLoading(false));
    }
  };




  useEffect(() => {
    getAllAppoint();
  }, []);

  const complete = async (ele) => {
    try {
      await toast.promise(
        axios.put(
          "/appointment/completed",
          {
            appointid: ele?._id,
            doctorId: ele?.doctorId?._id,
            doctorname: `${ele?.userId?.firstname} ${ele?.userId?.lastname}`,
          },
          {
            headers: { Authorization: `Bearer ${localStorage.getItem("token")}` },
          }
        ),
        {
          success: "Appointment completed",
          error: "Unable to complete appointment",
          loading: "Completing...",
        }
      );
      getAllAppoint();
    } catch (error) {}
  };

const downloadReceipt = async (ele) => {
  const receipt = document.createElement("div");

  // container styling
  receipt.style.width = "250px";
  receipt.style.padding = "16px 14px";
  receipt.style.borderRadius = "14px";
  receipt.style.fontFamily = "Poppins, Arial, sans-serif";
  receipt.style.color = "#0e0e0e";
  receipt.style.textAlign = "center";
  receipt.style.background = `
    linear-gradient(135deg, rgba(0,120,255,0.08) 0%, rgba(0,180,255,0.15) 100%)
  `;
  receipt.style.border = "1px solid rgba(0,120,255,0.4)";
  receipt.style.boxShadow = "0 3px 18px rgba(0,0,0,0.1)";
  receipt.style.backdropFilter = "blur(5px)";
  receipt.style.position = "fixed";
  receipt.style.top = "-9999px";
  receipt.style.backgroundColor = "transparent";

  // Doctor info from the table logic
const doctorName = `${ele?.doctorId?.firstname || ""} ${ele?.doctorId?.lastname || ""}`;


const doctorFullName = `Dr. ${doctorName}`;





  receipt.innerHTML = `
    <img src="${window.location.origin}/logo.png"
         style="width:60px;margin-bottom:6px;opacity:0.95;"
         alt="logo"/>

    <h3 style="margin:4px 0 2px 0;
               font-size:14px;
               color:#003366;
               letter-spacing:0.5px;
               font-weight:600;">
      Appointment Receipt
    </h3>
    <p style="margin:0;font-size:10px;color:#444;">UIZ Hospital </p>

    <hr style="margin:8px 0;border:none;border-top:1px dashed rgba(0,0,0,0.25);">

   <p style="margin:4px 0;font-size:12px;">
  <strong>Doctor:</strong> ${doctorFullName}
</p>




    <p style="margin:4px 0;font-size:12px;">
      <strong>Patient:</strong> ${ele?.userId?.firstname || ""} ${ele?.userId?.lastname || ""}
    </p>

    <p style="margin:4px 0;font-size:12px;">
      <strong>Date:</strong> ${ele?.date}
    </p>

    <p style="margin:4px 0;font-size:12px;">
      <strong>Time:</strong> ${ele?.time}
    </p>

    <p style="margin:4px 0;font-size:12px;">
      <strong>Status:</strong> ${ele?.status}
    </p>

    <hr style="margin:8px 0;border:none;border-top:1px dashed rgba(0,0,0,0.25);">

    <p style="font-size:9px;color:#555;margin-top:4px;">
      Generated on ${new Date().toLocaleString()}
    </p>

    <p style="font-size:8px;color:#999;margin-top:2px;">
      © UIZ Hospital | Digital Healthcare System
    </p>
  `;

  document.body.appendChild(receipt);

  try {
    const canvas = await html2canvas(receipt, {
      scale: 3,
      useCORS: true,
      backgroundColor: null,
    });

    const imgData = canvas.toDataURL("image/png");
    const pdf = new jsPDF({
      orientation: "portrait",
      unit: "mm",
      format: [75, 100],
    });

    pdf.addImage(imgData, "PNG", 4, 4, 67, 92);
    pdf.save(`Appointment_${ele?._id}.pdf`);
  } catch (error) {
    console.error("Error generating receipt PDF:", error);
  } finally {
    document.body.removeChild(receipt);
  }
};




  return (
    <>
      <Navbar />
      {loading ? (
        <Loading />
      ) : (
        <section className="container notif-section">
          <h2 className="page-heading">Your Appointments</h2>
          {appointments.length > 0 ? (
            <div className="appointments">
              <table>
                <thead>
                  <tr>
                    <th>S.No</th>
                    <th>Doctor</th>
                    <th>Patient</th>
                    <th>Appointment Date</th>
                    <th>Appointment Time</th>
                    <th>Booking Date</th>
                    <th>Booking Time</th>
                    <th>Status</th>
                    <th>Receipt</th>
                    {userId === appointments[0].doctorId?._id && <th>Action</th>}
                  </tr>
                </thead>
                <tbody>
  {appointments?.map((ele, i) => (
    <tr key={ele?._id}>
      <td data-label="S.No">{i + 1}</td>
      <td data-label="Doctor">{ele?.doctorId?.firstname + " " + ele?.doctorId?.lastname}</td>
      <td data-label="Patient">{ele?.userId?.firstname + " " + ele?.userId?.lastname}</td>
      <td data-label="Appointment Date">{ele?.date}</td>
      <td data-label="Appointment Time">{ele?.time}</td>
      <td data-label="Booking Date">{ele?.createdAt.split("T")[0]}</td>
      <td data-label="Booking Time">{ele?.updatedAt.split("T")[1].split(".")[0]}</td>
      <td data-label="Status">{ele?.status}</td>
      <td data-label="Receipt">
        <button
          onClick={() => downloadReceipt(ele)}
          className="btn user-btn"
          style={{
            background: "#0a84ff",
            color: "#fff",
            padding: "5px 10px",
            borderRadius: "6px",
            fontSize: "12px",
          }}
        >
          Download
        </button>
      </td>
      {userId === ele?.doctorId?._id && (
        <td data-label="Action">
          <button
            className={`btn user-btn accept-btn ${
              ele?.status === "Completed" ? "disable-btn" : ""
            }`}
            disabled={ele?.status === "Completed"}
            onClick={() => complete(ele)}
          >
            Complete
          </button>
        </td>
      )}
    </tr>
  ))}
</tbody>

              </table>
            </div>
          ) : (
            <Empty />
          )}
        </section>
      )}
      <Footer />
    </>
  );
};
export default Appointments;
